:root {
  --transform: cubic-bezier(.4, 0, .2, 1);
  --time-transform: .25s;
  --body-bg-light: #f6f6f6;
  --body-bg-dark: #202024;
  --header-bg-dark: #111;
  --header-bg-light: #fff;
  --violet: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --top-category-title: #b4afaf;
  --text-color: #111;
  --text-color-dark: #fff;
  --bgc-modal: #1116;
  --modal-text-light: #1116;
  --modal-text-dark: #ffffff80;
  --modal-text-autor: #b4afaf;
  --scroll-color: #e0e0e0;
  --text-shop-list: #fff9;
  --text-shop-list-light: #0009;
  --delete-button: #4f2ee8;
}

body.dark-theme {
  background-color: var(--body-bg-dark);
}

.dark-theme .header {
  border: 1.5px solid var(--white);
  background-color: var(--header-bg-dark);
}

.dark-theme .header-logo-text {
  color: var(--text-color-dark);
}

.dark-theme .icon-cross, .dark-theme .icon-menu {
  stroke: var(--text-color-dark);
}

.dark-theme .nav-item {
  color: var(--text-color-dark);
}

.dark-theme .nav-item-current {
  color: var(--text-color);
}

@media screen and (min-width: 767px) {
  .dark-theme .icon-sl-header {
    fill: var(--text-color-dark);
  }
}

.dark-theme .category-list::-webkit-scrollbar-thumb {
  border: 12px solid var(--scroll-color-dark);
}

.dark-theme .category-span {
  color: var(--text-color-span-dark);
}

.dark-theme .category-li .upper-case, .dark-theme .active {
  color: var(--yellow);
}

.dark-theme .books-section {
  color: var(--white);
}

.dark-theme .shop-title {
  color: var(--header-bg-light);
}

.dark-theme .text-shop-list {
  color: var(--text-shop-list);
}

.dark-theme .shopping-card {
  background-color: var(--text-color);
}

.dark-theme .book-card-title, .dark-theme .shopping-description {
  color: var(--header-bg-light);
}

.dark-theme .first-img, .dark-theme .modal-link:hover .first-img {
  filter: brightness(0) invert();
}

.dark-theme .modal-window {
  color: var(--text-color-dark);
  border: 2px solid var(--white);
  background-color: var(--body-bg-dark);
}

.dark-theme .modal-svg {
  stroke: var(--white);
}

.dark-theme .modal-autor {
  color: var(--top-category-title);
}

.dark-theme .modal-btn-main {
  color: var(--text-color-dark);
}

.dark-theme .modal-bottom-text {
  color: var(--modal-text-dark);
}

.dark-theme .modal-shops-item:first-child {
  filter: brightness(6);
}

@media screen and (min-width: 768px) {
  .dark-theme .modal-svg:hover {
    stroke: var(--violet);
  }

  .dark-theme .modal-shops-item:first-child:hover {
    filter: brightness(6) drop-shadow(0 7px 1px #fff3);
  }

  .dark-theme .modal-shops-item:hover {
    filter: drop-shadow(0 7px 1px #fff3);
  }
}

.dark-theme .form-window {
  background: var(--body-bg-dark);
  border: 2px solid var(--body-bg-light);
}

.dark-theme .form-input {
  border: 2px solid var(--body-bg-light);
  background-color: var(--body-bg-dark);
  color: var(--text-color);
}

.dark-theme .btn-form {
  color: var(--text-color-light);
  background-color: var(--body-bg-light);
}

.dark-theme .form-svg-icon {
  fill: var(--body-bg-dark);
  stroke: var(--white);
}

.dark-theme .footer-block, .dark-theme .promo-title, .dark-theme .team-style {
  color: var(--text-color-dark);
}

.dark-theme .team-info {
  background-color: #2c2c2c;
  border-radius: 5%;
}

.main-section-shop {
  margin-top: 104px;
}

.support {
  display: none;
}

.link-book-shop {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.shopping-list-title {
  letter-spacing: -1.28px;
  margin-bottom: 120px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.18;
}

.shop-title {
  color: var(--text-color);
}

.s-l-title {
  color: var(--violet);
}

.text-shop-list {
  color: var(--text-shop-list-light);
  text-align: center;
  letter-spacing: -.28px;
  margin-bottom: 14px;
  line-height: 1.28;
}

.message {
  width: 265px;
  height: 248px;
  margin: 0 auto;
}

.shopping-card {
  background-color: var(--header-bg-light);
  border: 2px solid #4f2ee866;
  border-radius: 16px;
  height: 170px;
  padding: 14px;
  display: flex;
  position: relative;
}

.shopping-image {
  border-radius: 8px;
  height: 142px;
  margin-right: 14px;
}

.books-sh-container {
  width: 193px;
}

.title-shop-list {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 144px;
  overflow: hidden;
}

.shopping-category {
  color: var(--top-category-title);
  letter-spacing: .36px;
  margin-bottom: 8px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.16;
}

.shopping-description {
  color: var(--text-color);
  letter-spacing: -.56px;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
  display: -webkit-box;
  overflow: hidden;
}

.shopping-description-container {
  height: 68px;
  margin-bottom: 16px;
}

.shopping-author {
  color: var(--top-category-title);
  letter-spacing: -.48px;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 96px;
  font-size: 12px;
  font-style: italic;
  line-height: 1.16;
  overflow: hidden;
}

.delete-button {
  background-color: var(--delete-button);
  border: none;
  border-radius: 50px;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 14px;
  right: 14px;
}

.delete-button:hover {
  filter: drop-shadow(0 7px 2px #5887ec33);
  scale: 1.3;
}

.shopping-svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-link .first-img {
  width: 32px;
  height: 11px;
}

.list-shops {
  align-items: center;
  gap: 8px;
  display: flex;
}

.modal-img-shop {
  width: 16px;
  height: 16px;
}

.book-list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.shopping-descr {
  width: 193px;
}

@media screen and (min-width: 768px) {
  .shopping-list-title {
    letter-spacing: -1.92px;
    margin-bottom: 140px;
    font-size: 48px;
    line-height: 1.08;
  }

  .text-shop-list {
    letter-spacing: -.36px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 1.33;
  }

  .message {
    width: 349px;
    height: 309px;
  }

  .shop-img {
    width: 322px;
    height: 241px;
    margin: 0 auto;
  }

  .shopping-image {
    width: 116px;
    height: 165px;
    margin-right: 24px;
  }

  .book-card-title {
    width: 166px;
  }

  .shopping-description-container {
    height: 53px;
    margin-bottom: 32px;
  }

  .shopping-card {
    height: 213px;
    padding: 24px;
  }

  .modal-link .first-img {
    width: 48px;
    height: 15px;
  }

  .modal-img-shop {
    width: 28px;
    height: 27px;
  }

  .list-shops {
    gap: 16px;
  }

  .shopping-author, .book-card-title {
    white-space: normal;
    text-overflow: initial;
    width: auto;
    overflow: visible;
  }

  .shopping-descr {
    width: 508px;
  }
}

@media screen and (min-width: 1440px) {
  .main-section-shop {
    gap: 40px;
    margin-top: 130px;
    display: flex;
  }

  .support {
    display: block;
  }

  .shopping-list-title {
    margin-bottom: 140px;
  }

  .message {
    padding: 10px;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateY(-50%);
  }

  .shopping-description {
    white-space: normal;
    text-overflow: initial;
    width: auto;
    overflow: visible;
  }

  .shopping-descr {
    width: 808px;
  }
}

